
* {
  margin: 0;
  padding: 0;
}
a {
  color: #000;
  text-decoration: none;
}
a:active,
a:hover {
  outline: 0;
}
.clear {
  clear: both;
  visibility: hidden;
  margin: 0;
  padding: 0;
}
html,
body {
  font-size: 1em;
  line-height: 1.2em;
}
/* 顶部阴影效果 */
body:before {
  content: "";
  position: fixed;
  top: -0.8em;
  left: 0;
  width: 100%;
  height: 1em;
  -webkit-box-shadow: 0 0 1em rgba(0, 0, 0, 0.8);
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.8);
  z-index: 10;
}
ul {
  display: block;
  list-style: none;
}
.cd-timeline-block h2 {
  line-height: 1.2em;
}
/* nav */
#nav {
  width: 100%;
  margin: 1em auto;
  padding: 0;
}
#nav ul {
  display: block;
  width: 100%;
  list-style: none;
}
#nav li {
  width: 100%;
  display: block;
}
#nav a {
  color: #000;
  font-weight: 700;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: background .3s ease;
  transition: background .3s ease;
  text-shadow: 0 -1px rgba(0, 0, 0, 0.5);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  display: block;
  padding: .6em 2em;
  width: 100%;
}
#nav .active a {
  color: #fff;
  background: rgba(0, 0, 0, 0.6);
}
#nav li:first-child a {
  border-top: 0;
}
#nav li:last-child a {
  border-bottom: 0;
}
#nav a:hover {
  background: rgba(0, 0, 0, 0.4);
}
#nav .active a:hover {
  color: #000;
  background: rgba(207, 207, 207, 0.8);
}
div {
  padding: 1%;
}
.avatar {
  display: block;
  border-radius: 50%;
  width: 8em;
  margin: 2em auto 0.4em;
}
#wrapper {
  min-height: 100%;
  padding: 0;
  width: 100%;
}
header {
  width: 100%;
  text-align: center;
  background: #333;
  color: #ccc;
}
#contact {
  width: 100%;
  margin: 2em auto 0;
  padding: 0 0.4em;
  position: relative;
  bottom: 0;
  text-align: center;
}
#contact > address {
  display: inline-block;
}
.social-icon {
  width: 24px;
  height: 24px;
  margin: 2px;
  display: inline;
}
footer .social-icon:hover {
  width: 26px;
}
#sidebar {
  width: 22%;
  float: left;
  padding-bottom: 0;
}
#topside {
  text-align: center;
  width: 100%;
  height: auto;
  min-height: 6em;
}
#main {
  float: left;
  padding-left: 0.8em;
  width: 76%;
}
.title {
  font-size: 1.4em;
  line-height: 1.4em;
  font-weight: bolder;
  font-style: italic;
  text-shadow: 0 0 1em rgba(0, 0, 0, 0.3);
  width: 100%;
}
.title img {
  width: 1.2em;
  display: inline;
  vertical-align: middle;
}
.subtitle {
  font-size: 1.2em;
  margin-top: 0.4em;
  display: block;
  font-weight: bold;
  padding: 0.1em auto 0.3em;
  line-height: 1.3em;
}
.box {
  float: left;
  padding: 0.2em;
  width: 31%;
  font-style: italic;
  margin-top: 1em;
  text-align: center;
  color: #fff;
}
.box:nth-child(3n+2) {
  border: none;
  background: #D13438;
}
.box:nth-child(3n) {
  background: #107C10;
}
.box:nth-child(3n+1) {
  background: #0078D7;
}
.projectList {
  list-style-position: outside;
  list-style-type: decimal;
}
.techTag ul {
  list-style-type: disc;
}
.projectTitle {
  font-size: 1.2em;
  line-height: 1.2em;
  font-weight: bold;
}
.skillList {
  list-style: none;
}
.skillList li {
  line-height: 3em;
  float: left;
  width: 10em;
  margin-left: 1em;
}
.skillList li:hover {
  float: left;
  width: 10em;
  margin-left: 1.2em;
}
.progress {
  width: 10em;
  background-color: #ccc;
  height: 2px;
  padding: 0;
}
.progress span {
  height: 100%;
  background-color: #e33434;
  display: block;
}
.rateList li {
  line-height: 1.2em;
}
@media only screen and (max-width: 1280px) {
#sidebar {
    width: 30%;
    min-width: 300px;
}
#main {
    width: 66%;
    min-width: 100px;
}
}
@media only screen and (max-width: 882px) {
#sidebar {
    width: 48%;
    min-width: 300px;
}
#main {
    width: 48%;
}
}
@media only screen and (max-width: 576px) {
#sidebar {
    width: 60%;
    min-width: 300px;
}
#main {
    width: 36%;
}
}
/*mobile layout*/
@media only screen and (max-width: 480px) {
#sidebar {
    width: 100%;
    margin: 1em auto;
}
#main {
    width: 100%;
}
}
/* timeline */
html * {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*,
*:after,
*:before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
img {
  max-width: 100%;
}
/* --------------------------------

Modules - reusable parts of our design

-------------------------------- */
.cd-container {
  /* this class is used to give a max-width to the element it is applied to, and center it horizontally when it reaches that max-width */
  width: 90%;
  max-width: 1170px;
  margin: 0 auto;
}
.cd-container::after {
  /* clearfix */
  content: '';
  display: table;
  clear: both;
}
/* --------------------------------

Main components

-------------------------------- */
#cd-timeline {
  position: relative;
  padding: 2em 0;
  margin-top: 2em;
  margin-bottom: 2em;
}
#cd-timeline::before {
  /* this is the vertical line */
  content: '';
  position: absolute;
  top: 0;
  left: 18px;
  height: 100%;
  width: 4px;
  background: #d7e4ed;
}
@media only screen and (min-width: 1170px) {
#cd-timeline {
    margin-top: 3em;
    margin-bottom: 3em;
}
#cd-timeline::before {
    left: 50%;
    margin-left: -2px;
}
}
.cd-timeline-block {
  position: relative;
  margin: 2em 0;
}
.cd-timeline-block:after {
  content: "";
  display: table;
  clear: both;
}
.cd-timeline-block:first-child {
  margin-top: 0;
}
.cd-timeline-block:last-child {
  margin-bottom: 0;
}
@media only screen and (min-width: 1170px) {
.cd-timeline-block {
    margin: 4em 0;
}
.cd-timeline-block:first-child {
    margin-top: 0;
}
.cd-timeline-block:last-child {
    margin-bottom: 0;
}
}
.cd-timeline-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  -webkit-box-shadow: 0 0 0 4px #ccc, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);
          box-shadow: 0 0 0 4px #ccc, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);
}
.cd-timeline-img img {
  display: block;
  width: 24px;
  height: 24px;
  position: relative;
  left: 50%;
  top: 50%;
  margin-left: -12px;
  margin-top: -12px;
}
.cd-timeline-img {
  background: #75ce66;
}
.cd-timeline-img.cd-picture {
  background: #75ce66;
}
.cd-timeline-img.cd-movie {
  background: #c03b44;
}
.cd-timeline-img.cd-location {
  background: #f0ca45;
}
@media only screen and (min-width: 1170px) {
.cd-timeline-img {
    width: 60px;
    height: 60px;
    left: 50%;
    margin-left: -30px;
    /* Force Hardware Acceleration in WebKit */
    -webkit-transform: translateZ(0);
    -webkit-backface-visibility: hidden;
}
.cssanimations .cd-timeline-img.is-hidden {
    visibility: hidden;
}
.cssanimations .cd-timeline-img.bounce-in {
    visibility: visible;
    -webkit-animation: cd-bounce-1 0.6s;
    animation: cd-bounce-1 0.6s;
}
}
.cd-timeline-content {
  position: relative;
  margin-left: 60px;
  background: #ccc;
  border-radius: 0.25em;
  padding: 1em;
  -webkit-box-shadow: 0 3px 0 #d7e4ed;
          box-shadow: 0 3px 0 #d7e4ed;
}
.cd-timeline-content:after {
  content: "";
  display: table;
  clear: both;
}
.cd-timeline-content h2 {
  color: #303e49;
}
.cd-timeline-content p,
.cd-timeline-content .cd-read-more,
.cd-timeline-content .cd-date {
  font-size: 13px;
  font-size: 0.8125rem;
}
.cd-timeline-content .cd-read-more,
.cd-timeline-content .cd-date {
  display: inline-block;
}
.cd-timeline-content p {
  margin: 1em 0;
  line-height: 1.6;
}
.cd-timeline-content .cd-read-more {
  float: right;
  padding: .8em 1em;
  background: #acb7c0;
  color: #ccc;
  border-radius: 0.25em;
}
.no-touch .cd-timeline-content .cd-read-more:hover {
  background-color: #bac4cb;
}
a.cd-read-more:hover {
  text-decoration: none;
  background-color: #424242;
}
.cd-timeline-content .cd-date {
  float: left;
  padding: .8em 0;
  opacity: .7;
}
.cd-timeline-content::before {
  content: '';
  position: absolute;
  top: 16px;
  right: 100%;
  height: 0;
  width: 0;
  border: 7px solid transparent;
  border-right: 7px solid #ccc;
}
@media only screen and (min-width: 768px) {
.cd-timeline-content h2 {
    font-size: 20px;
    font-size: 1.25rem;
}
.cd-timeline-content p {
    font-size: 16px;
    font-size: 1rem;
}
.cd-timeline-content .cd-read-more,
  .cd-timeline-content .cd-date {
    font-size: 14px;
    font-size: 0.875rem;
}
}
@media only screen and (min-width: 1170px) {
.cd-timeline-content {
    margin-left: 0;
    padding: 1.6em;
    width: 45%;
}
.cd-timeline-content::before {
    top: 24px;
    left: 100%;
    border-color: transparent;
    border-left-color: #ccc;
}
.cd-timeline-content .cd-read-more {
    float: left;
}
.cd-timeline-content .cd-date {
    position: absolute;
    width: 100%;
    left: 122%;
    top: 6px;
    font-size: 16px;
    font-size: 1rem;
}
.cd-timeline-block:nth-child(even) .cd-timeline-content {
    float: right;
}
.cd-timeline-block:nth-child(even) .cd-timeline-content::before {
    top: 24px;
    left: auto;
    right: 100%;
    border-color: transparent;
    border-right-color: #ccc;
}
.cd-timeline-block:nth-child(even) .cd-timeline-content .cd-read-more {
    float: right;
}
.cd-timeline-block:nth-child(even) .cd-timeline-content .cd-date {
    left: auto;
    right: 122%;
    text-align: right;
}
.cssanimations .cd-timeline-content.is-hidden {
    visibility: hidden;
}
.cssanimations .cd-timeline-content.bounce-in {
    visibility: visible;
    -webkit-animation: cd-bounce-2 0.6s;
    animation: cd-bounce-2 0.6s;
}
}
@media only screen and (min-width: 1170px) {
  /* inverse bounce effect on even content blocks */
.cssanimations .cd-timeline-block:nth-child(even) .cd-timeline-content.bounce-in {
    -webkit-animation: cd-bounce-2-inverse 0.6s;
    animation: cd-bounce-2-inverse 0.6s;
}
}
